.header {
    background: var(--primary-background-color);
    margin-left: auto;
    margin-right: auto;
    &-nav {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 41px;
    }
    &>.container {
        background-repeat: no-repeat;
        background-position: center;
        background-origin: border-box;
        background-size: cover;
        padding-top: 46px;
        padding-bottom: 96px;
        width: 100%;
        @media screen and (min-width: $MobileWidth) {
            background-image: linear-gradient(rgba(00, 00, 00, 0.56), rgba(00, 00, 00, 0.56)), url(../images/header/header-bg.jpg);
        }
        @media screen and (min-width: $TabletWidth) {
            background-image: linear-gradient(rgba(00, 00, 00, 0.56), rgba(00, 00, 00, 0.56)), url(../images/header/header-bg-tabl.jpg);
        }
        @media screen and (min-width: $DesktopWidth) {
            background-image: linear-gradient(rgba(00, 00, 00, 0.56), rgba(00, 00, 00, 0.56)), url(../images/header/header-bg-desk.jpg);
        }
    }
}

.logo {
    color: var(--primary-text-color);
    font-family: Roboto;
    font-style: normal;
    font-weight: 500;
    font-size: 30px;
    line-height: 35px;
    display: flex;
    align-items: center;
    text-align: center;
    width: 24px;
    height: 24px;
    &-name {
        @media screen and (max-width: 770px - 1px) {
            display: none;
        }
    }
    &-img {
        margin-right: 10px;
    }
    &-links {
        display: flex;
        flex-wrap: nowrap;
    }
    &-link {
        color: var(--primary-text-color);
    }
    &-item:not(:last-child) {
        margin-right: 40px;
    }
    &-current {
        border-bottom: 3px solid var(--accent-color);
    }
}

.search {
    @media screen and (min-width: 770px) {
        display: flex;
        justify-content: center;
    }
    &-input {
        margin-right: auto;
        margin-left: auto;
        @media screen and (max-width: 770px - 1px) {
            width: 100%;
        }
        @media screen and (min-width: 770px) {
            width: 336px;
        }
        width: 100%;
        background-color: inherit;
        border-bottom: 0.5px solid var(--primary-text-color);
        color: var(--primary-text-color);
        outline: none;
        border-top: none;
        border-left: none;
        border-right: none;
        padding-bottom: 5px;
    }
    &-input:focus,
    &-input:active {
        border-bottom: 0.5px solid var(--accent-color);
    }
    &-input:focus::placeholder,
    &-input:active::placeholder {
        color: var(--accent-color);
    }
    &-input::placeholder {
        color: var(--primary-text-color);
    }
}